import {IOpsSdk, OpsPortalEvent} from '@deliveryhero/opsportal';
import {Typography} from "@material-ui/core";
import React, { useEffect } from 'react';
import {findFlagUrlByIso2Code} from "country-flags-svg";
import './App.css';

export const App: React.FC<{ baseApi: IOpsSdk }> = ({baseApi}) => {

    let country = baseApi.getCountry();
    const cntChangeListener = (e: OpsPortalEvent) => { country = e.data;};

    useEffect(() => {
        baseApi.addEventListener('countryChange', cntChangeListener);
        return () => baseApi.removeEventListener('countryChange', cntChangeListener);
    });

    return (
        <div className="countryPlugin">
            <Typography variant="h1">{country.name} </Typography>
            <br/>
            <img height="500" src={findFlagUrlByIso2Code(country.code)} />
        </div>
    );
}

export default App;
